@import "https://fonts.googleapis.com/css?family=Pinyon+Script&display=swap";
@import "https://fonts.googleapis.com/css?family=Playfair+Display&display=swap";
@import "https://fonts.googleapis.com/css?family=Playfair+Display:700&display=swap";
@import "https://fonts.googleapis.com/css?family=Dancing+Script&display=swap";
body {
  min-width: 320px;
  max-width: 1024px;
  background-color: #eee;
  margin: auto;
  font-family: Playfair Display, serif;
  font-size: 1rem;
}

blockquote {
  color: #555;
  font-family: Dancing Script, cursive;
  font-size: 1.5rem;
}

hr {
  border: .5px dotted #bbb;
}

#article {
  width: 85%;
  height: 100%;
  background-color: #fff;
  margin: auto;
  padding: 20px;
  box-shadow: 0 0 10px #ccc;
}

#title {
  text-align: center;
  font-family: Pinyon Script, cursive;
  font-size: 4rem;
}

#img-div {
  text-align: center;
  margin: auto;
}

#img-div img {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: block;
  box-shadow: 0 5px 10px #ccc;
}

#img-caption {
  margin-top: 5px;
  font-size: .9rem;
  font-style: italic;
}

#tribute-info {
  text-align: center;
  width: 75%;
  margin: auto;
}

#tribute-info ul {
  text-align: left;
}

#intro {
  margin: 30px auto;
  font-size: 1rem;
  font-style: italic;
  font-weight: bold;
}

#quote {
  background-image: url("https://www.damienvassart.com/test/fcc/responsive/img/qmarks.png");
  background-repeat: no-repeat;
  background-size: contain;
  margin: 70px auto 50px;
}

@media screen and (max-width: 440px) {
  #title {
    text-align: center;
    font-family: Pinyon Script, cursive;
    font-size: 2rem;
  }

  #img-caption {
    font-size: .6rem;
  }

  #intro {
    font-size: .8rem;
  }
}

@media screen and (min-width: 441px) and (max-width: 768px) {
  #title {
    text-align: center;
    font-family: Pinyon Script, cursive;
    font-size: 2.9rem;
  }

  #img-caption {
    font-size: .8rem;
  }

  #intro {
    font-size: .9rem;
  }
}

